section#nav {
  min-height: 100px;
  box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.15);
}

section#nav .wrapper {
  display: flex;
  justify-content: space-between;
}

section#nav .logo {
  margin-top: 10px;
}

section#nav .logo a {
  text-decoration: none;
}

section#nav .logo p {
  text-align: center;
  color: #5363db;
  font-size: 2.4rem;
  font-family: 'Champagne & Limousines Bold';
  font-weight: normal;
}

section#nav .navigation ul li {
  list-style-type: none;
  display: inline-block;
  padding: 20px;
  padding-top: 37px;
}

section#nav .navigation a {
  color: #555555;
  font-size: 1.4rem;
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  text-decoration: none;
}

section#nav .navigation a:hover {
  transition: 0.3s;
  color: #5363db;
}

section#nav .navigation a:hover:after {
  content: "";
  background-color: #5363db;
  display: block;
  width: 2.5rem;
  height: 1px;
  margin: 2px auto;
  transition: 0.3s;
}

section#nav .menu-link {
  z-index: 9;
  width: 50px;
  height: 50px;
  display: none;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.4);
  position: fixed;
  right: 20px;
  top: 30px;
}

section#nav .menu-link span,
section#nav .menu-link span:before,
section#nav .menu-link span:after {
  display: block;
  height: 2px;
  width: 18px;
  right: 50%;
  margin-right: -9px;
  background-color: #333;
  position: absolute;
  top: 50%;
  margin-top: -1px;
}

section#nav .menu-link span:before {
  transition: all 0.2s;
  content: '';
  margin-top: -6px;
}

section#nav .menu-link span:after {
  transition: all 0.2s;
  content: '';
  margin-top: 4px;
}

section#nav .menu-link_active span {
  background-color: transparent;
}

section#nav .menu-link_active span:before {
  margin-top: 0;
  transform: rotate(135deg);
}

section#nav .menu-link_active span:after {
  margin-top: 0;
  transform: rotate(-135deg);
}

section#nav .menu {
  opacity: 0.9;
  z-index: 1;
  height: 100vh;
  width: 100vw;
  position: fixed;
  background-color: #7885e4;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  right: -100vw;
  transition: all 0.4s;
}

section#nav .menu-active {
  right: 0;
}

section#nav .menu ul {
  text-align: center;
  list-style: none;
}

section#nav .menu ul li {
  width: 100%;
}

section#nav .menu ul a {
  font-size: 1.5rem;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  text-decoration: none;
  display: block;
  width: 110vw;
  font-weight: 100;
  letter-spacing: 4px;
  line-height: 4;
  color: #fff;
  transition: all 0.2s;
}

section#nav .menu ul a:hover {
  transition: all 0.2s;
  background-color: rgba(255, 255, 255, 0.4);
}

header {
  position: relative;
  z-index: 5;
  color: #fff;
  height: 100vh;
  min-height: 700px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

header .wrap {
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #414140;
  background-position: center;
  opacity: 0.7;
  width: 100%;
  height: 100%;
}

/*==========  Desktop First Method  ==========*/

/*==========  Desktop First Method  ==========*/

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 960px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

html {
  font-size: 10px;
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}

@media screen and (max-width: 380px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}

@media (min-width: 0px) and (max-width: 655px) {
  section#nav {
    min-height: 55px;
  }

  section#nav .logo {
    display: block;
  }

  section#nav .navigation {
    display: none;
  }

  section#nav .menu-link {
    display: block;
    margin-top: -10px;
  }
}

@media (min-width: 656px) and (max-width: 1920px) {
  .menu {
    display: none !important;
  }
}